.wrapper {
  text-align: center;
  display: flex;
  width: 100%;
  flex-direction: column;
  max-width: 1200px;
  margin: 20px auto;
}
html {
  font-family: Arial, sans-serif;
  margin: 0;
  box-sizing: border-box;
}

.nav {
  background-color: #333;
  color: white;
  padding: 5px 20px;
}

.list {
  width: 100%;
  text-align: left;
  padding: 0;
  border-bottom: 1px solid #ccc;
}

.title {
  font-size: 20px;
  font-weight: 600;
  margin: 20px 0;
}

.modal {
  border: 1px solid #888;
  position: fixed;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* 중앙으로 이동 */
  width: 800px;
  height: 600px;
  background-color: rgb(255, 255, 255);
}

button {
  font-weight: 600;
  background-color: #5276e9;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  margin: 10px 10px;
}

input {
  font-size: 18px;
  font-weight: 400;
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  border: none;
  padding: 6px 10px;
  border-radius: 5px;
  margin: 10px 10px;
  border: 2px solid #000000;
}

.make {
  display: flex;
  justify-content: center;
}

.make input {
  width: 60%;
}

.make button {
  width: 100px;
}
